@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
    box-sizing: border-box;
}


.blog-slider {
    width: 95%;
    position: relative;
    max-width: 800px;
    margin: auto;
    background-image: -webkit-linear-gradient(303deg, #9936cc 0%, #941d49 74%);
    background-image: linear-gradient(147deg, #9936cc 0%, #941d49 74%);
    background: #fff;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
    padding: 25px;
    border-radius: 25px;
    height: 400px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

@media screen and (max-width: 992px) {
    .blog-slider {
        max-width: 680px;
        height: 400px;
    }
}

@media screen and (max-width: 768px) {
  .blog-slider {
    min-height: 500px;
    height: auto;
    margin: 180px auto;
  }
}

@media screen and (max-height: 500px) and (min-width: 992px) {
  .blog-slider {
    height: 350px;
  }
}

.blog-slider__item {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .blog-slider__item {
    flex-direction: column;
  }
}

